.main {
    width: 100%;
    height: auto;
    /* margin-top: 90px; */
    background-color: black;
}

.dingwei {
    height: 20vw;
    background-image: url(../../../../imgs/我的产品.webp);
    background-size: cover;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    align-items: center;
    position: relative;
    top: 0;
    left: 0;
}

.displayCenter {
    font-size: 53px;
    font-weight: bold;
    letter-spacing: normal;
    font-family: poppins-semibold, poppins, sans-serif;
    color: #FFFFFF;
    line-height: 1.2em;
    text-align: center;

}

.displayCenter div {
    align-items: center;
    text-align: center;
}



.main .new_p {
    padding: 0 auto;
    text-align: left;
    margin: 0 auto;
    /* margin: 2px 20vw; */
    color: #CCCCCC;
    font-size: 0.2rem;
    line-height: 0.375rem;
    font-weight: 100;
    display: -webkit-box;
    margin: 3vw 20vw;
    text-indent:2em;
}

.imgStyle {
    width: 100%;
    z-index: -1;
    position: absolute;
    top: 0;
    left: 0;
}

.imgSrcS {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 50px;
}

.imgSrcLi {
    width: 50vw;
    height: calc(50vw * 443 / 758);
    background-color: black;
    flex-basis: calc(50%);
    padding: 3vw 10% 0 10%;
    text-align: center;

}

.ingSrcTwo {
    height: calc(50vw * 443 / 758);
    flex-basis: calc(50%);
}

.ingSrcTwo img {
    width: 100%;
    height: 100%;
}

.imgSrcLi span {
    color: #FFFFFF;
    font-size: 0.50rem;
    font-weight: 600;
}

.imgSrcLi .spanLi {
    text-indent: 2em;
    position: relative;
    display: inline-block;
    text-align: left;
    margin: 0 auto;
    margin-top: 20px;
    color: #CCCCCC;

    font-size: 0.2rem;
    line-height: 0.375rem;
    font-weight: 100;
    display: -webkit-box;
    /* ///使用了flex，需要加 */
    overflow: hidden;
    /* //超出隐藏 */
    word-break: break-all;
    /* //纯英文、数字、中文 */
    text-overflow: ellipsis;
    /* //省略号 */
    -webkit-box-orient: vertical;
    /* //垂直 */
    -webkit-line-clamp: 9;
    /* //显示一行 */
    white-space: pre-line;
    /* //合并空白符序列，但是保留换行符,如果要使用这个垂直和显示几行的代码也要写 */

}