.main {
    width: 100vw;
    height: 80px !important;
    background-color: black;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.icon {
    color: #bebebf;
}

.overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 0;
    background-color: rgb(0, 0, 0);
    opacity: 0;
}

.overlay .overlayIcon {
    height: 80px !important;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.navLinkStyle {
    display: flex;
    flex-wrap: wrap;
}

.navLinkStyle a {
    /* height: 2rem; */
    width: 100vw;
}