
.icon {
    color: #bebebf;
}

.overlay {  
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgb(0, 0, 0);
    opacity: 1;
}

.overlay .overlayIcon {
    height: 80px !important;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.navLinkStyle {
    background-color: black;
    display: flex;
    flex-wrap: wrap;
}

.navLinkStyle a {
    height: 2rem;
    width: 100vw;
    font-size: 0.7rem;
}