.main {
    width: 100vw;
    margin-top: -50px;
    padding-top: 50px;
    background-color: black;
    display: flex;
    /* justify-content: space-around; */
    flex-wrap: nowrap;
    height: 300px;
}

.main div {
    flex: 1;
}

.svgSize {
    width: 2.575rem;
    height: 0.6125rem;
}

.svgBottomSpan {
    margin-top: 0.25rem;
    vertical-align: baseline;
    letter-spacing: normal;
    line-height: 1.6em;
    font-size: 14px;
    color: #9d9d9d;
}

.spanTwo {
    margin-top: 5%;
    padding-left: 15%;
    text-align: left;
    color: #57A7D8;
    font-size: 0.2rem;
    margin-bottom: 0.2rem;
}

.spanTwoColor {
    padding-left: 15%;
    font-size: 18px;
    line-height: 0.5rem;
    color: #9d9d9d;
    text-align: left;
}

.spanTwoColor img {
    border-radius:5px ;
    width: 105px;
    height: 105px;
}

.inputEmailFar {
    z-index: -1;
    display: inline-block;
    margin: 0 auto;
}

.inputEmail {
    display: inline-block;
}

.antInput::placeholder {
    color: #ffff;
}

.buttonHover:hover {
    /* font-weight: 900 !important; */
    color: black !important;
    background-color: #889ce7 !important;
}
/* 居中调试 */
#gzwm,#idimg {
    margin-right: 10vw;
    text-align: center;
}