.main {
    width: 100vw;
    /* background-color: black; */
    color: #fff;
}

.main .position {
    height: calc(100vw * 246 / 640);
    position: relative;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: .7rem;
    font-weight: 600;
}

.main .position img {
    height: 100%;
    z-index: -1;
    position: absolute;
    top: 0;
    left: 0;
}

.main .new_p {
    width: 100%;
    height: 100%;
    background-color: black;
    padding: 5vw 10%;
    margin: 0 auto;
    color: #CCCCCC;
    /* font-size: 0.4rem; */
    line-height: 0.375rem;
    font-weight: 100;
    display: -webkit-box;

    font-weight: 200;
    font-size: 16px;
    line-height: 1.8em;
    text-align: left;
    text-indent: 2em;
    color: rgb(138, 138, 138);
}


.imgDiv {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: center;
    background-color: black;
}

.imgDiv span {
    width: 80%;
}

.imgDiv img {
    width: 80%;
    margin-top: 10%;
    margin-bottom: 20%;
}

.imgDiv span {
    font-size: 0.5rem;
    font-weight: 600;
}

.imgDiv p {
    margin: 10%;
    font-weight: 200;
    font-size: 16px;
    line-height: 1.8em;
    text-align: left;
    text-indent: 2em;
    color: rgb(138, 138, 138);
}