.main {
  width: 100%;
  /* background-color: black; */
  position: relative;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.main .mainBac {
  position: absolute;
  left: 0;
  top: 0;
  z-index: -10;
  height: 100%;
}

.imgSrcLi {
  z-index: -9;
  flex-basis: calc(50%);
  padding-top: 6%;
  text-align: center;
}

.imgSrcLi span {
  color: #FFFFFF;
  font-size: 43px;
  letter-spacing: 0.05em;
  line-height: 0px;
  text-align: center;
  font-weight: 600;
}

.imgSrcLi .spanLi {
  text-align: center;
  /* text-indent: 2em; */
  margin: 0 auto;
  margin-top: 20px;
  max-width: 1000px;
  color: #efefef;
  font-size: 16px;
  line-height: 30px;
  font-weight: 100;
}

.imgSrcS {
  display: flex;
  flex-wrap: wrap;
  padding: 5% 2% 5% 2%;
}

.imgSrcS div {
  width: 20%;
  align-items: center;
  margin-bottom: 3%;
}

.imgSrcS div img {
  width: 7vw;
}

/* 设置初始状态 */
#animatedElement {
  position: relative;
  left: -100px;
  /* 初始位置在左侧 */
  opacity: 0;
  /* 初始透明度为0 */
}

/* 定义动画关键帧 */
@keyframes slideAndFadeIn {
  0% {
    left: -100px;
    /* 初始位置 */
    opacity: 0;
    /* 初始透明度 */
  }

  100% {
    left: 0;
    /* 最终位置在右侧 */
    opacity: 1;
    /* 最终透明度为1 */
  }
}

/* 应用动画 */
#animatedElement {
  animation: slideAndFadeIn 1.5s ease-in-out forwards;
  /* 持续时间为1秒，缓动效果，保持最终状态 */
}