.main {
    font-family: madefor-display,helveticaneuew01-45ligh,helveticaneuew02-45ligh,helveticaneuew10-45ligh,sans-serif;
    width: 100vw;
}

.main img {
    width: 100vw;
}

.father {
    height: calc(100vw * 320 / 375);
    position: relative;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    /* align-items: center; */
    padding-top: 27%;
    padding-bottom:30%;
    top: 0;
    left: 0;
    color: #FFFFFF;
    font-weight: 600;
}
.father span {
    width: 100vw;
}

.father img {
    z-index: -1;
    position: absolute;
    top: 0;
    left: 0;
}
/* language */
.language {
    margin-top: -6px;
    width: 100%;
    height: calc(100vw * 11 / 32);
    background-color: black;
    color: #FFFFFF;
    font-size: .6rem;
    display: flex;
    justify-content: center;
    align-items: center;
    font-style: normal;
    font-weight: 400;
}
.language span {
    width: 80%;
}
.languageP {
    padding: 20px 0 20px 0;
    margin-top: -6px;
    background-color: black;
    color: #FFFFFF;
    font-size: .3rem;
    font-weight: 300;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    align-items: center;

}
.languageP p{
    line-height: .5rem;
color: rgb(138,138,138);
width: 86%;
}
.languageP img{
    width: 70px;
    height: 70px;
    margin-bottom: 10%;
}
