.main {
  width: 100%;
}

* {
  margin: 0;
  padding: 0;
}

.displayCenter {
  position: relative;
  font-size: 1rem;
  font-weight: bold;
  letter-spacing: normal;
  font-family: poppins-semibold, poppins, sans-serif;
  color: #FFFFFF;
  line-height: 1.2em;
  text-align: center;
  top: 200px;
  left: 0px;
}

.videoBox {
  margin-top: -500px;
  position: relative;
  /* top: -400px; */
  z-index: -10;
  height: 990px;
  background-color: #C1CFF7;
  /*进行视频裁剪*/
  overflow: hidden;
}

.videoBox .videoBackground {
  position: absolute;
  left: 50%;
  top: 50%;
  /*保证视频内容始终居中*/
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  /*保证视频充满屏幕*/
  object-fit: cover;
  min-height: 800px;
}

.imgSrcS {
  display: flex;
  flex-wrap: wrap;
}

.imgSrcLi {
  height: 443px;
  background-color: black;
  flex-basis: calc(50%);
  padding-top: 160px;
}

.imgSrcLi span {
  display: inline-block;
  padding: 0 15% 0 15%;
  color: #FFFFFF;
  font-size: 0.7rem;
  letter-spacing: 0.05em;
  line-height: 80px;
  text-align: center;
  /* 字体换行 */
}

.ingSrcTwo {
  height: 443px;
  flex-basis: calc(50%);
}

.ingSrcTwo img {
  width: 100%;
  height: 100%;
}

.imgSrcStyle {

  height: 620px;
  height: calc(100vw * 620 / 1536);
  background-color: black;
  background-image: url(../../../../imgs/4.webp);
  background-size: 103%;
  width: 100%;
  margin-top: -150px;
}

.inputEmailFar {
  z-index: 99;
  display: inline-block;
  margin: 0 auto;
  position: relative;
  right: -100px;
  /* 初始位置在右侧 */
  opacity: 0;
  /* 初始透明度为0 */
}

/* 定义动画关键帧 */
@keyframes slideAndFadeInRight {
  0% {
    right: -100px;
    /* 初始位置 */
    opacity: 0;
    /* 初始透明度 */
  }

  100% {
    right: 0;
    /* 最终位置在左侧 */
    opacity: 1;
    /* 最终透明度为1 */
  }
}

/* 应用动画 */
.inputEmailFar {
  animation: slideAndFadeInRight 2s ease-in-out forwards;
  /* 持续时间为2秒，缓动效果，保持最终状态 */
}


.inputEmail {
  display: inline-block;
}

/* 设置初始状态 */
#animatedElement {
  z-index: -1;
  position: relative;
  left: -100px;
  /* 初始位置在左侧 */
  opacity: 0;
  /* 初始透明度为0 */
  /* background-color: #accadb; */
}

/* 定义动画关键帧 */
@keyframes slideAndFadeIn {
  0% {
    left: -100px;
    /* 初始位置 */
    opacity: 0;
    /* 初始透明度 */
  }

  100% {
    left: 0;
    /* 最终位置在右侧 */
    opacity: 1;
    /* 最终透明度为1 */
  }
}

/* 应用动画 */
#animatedElement {
  animation: slideAndFadeIn 2s ease-in-out forwards;
  /* 持续时间为1秒，缓动效果，保持最终状态 */
}

.buttonHover:hover {
  font-weight: 600 !important;
  color: #ffffff !important;
  background-color: black !important;
  border: 2px solid #ccc !important;
}