.main {
    width: 100vw;
    color: #fff;
}


.imgDiv {
    padding-bottom: 10%;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: center;
    background-color: black;
}

.imgDiv img {
    width: 55%;
    margin-top: 10%;
    margin-bottom: 20%;
}

.imgDiv span {
    margin-top: 10%;
    font-size: .8rem;
    font-weight: 600;
}

.imgDiv p {
    margin: 10%;
    font-weight: 200;
    font-size: 16px;
    line-height: 1.8em;
    text-align: center;
    /* text-indent: 2em; */
    color: #fff;
}

.imgDiv img {
    width: 30%;
    margin: 3% 6% 3% 3%;
}


.animatedElement {
    position: relative;
    bottom: 0;
    /* 初始位置在底部，可以根据需要调整 */
    opacity: 0;
    animation: slideUpAndFadeIn 1s ease-out forwards;
    /* 使用关键帧动画 slideUpAndFadeIn */
}

@keyframes slideUpAndFadeIn {
    0% {
        opacity: 0;
        transform: translateY(100%);
        /* 元素初始位置，假设容器高度是100% */
    }

    100% {
        opacity: 1;
        transform: translateY(0);
        /* 元素最终位置 */
    }
}