.main {
    width: 100vw;
    position: fixed;
    top: 0;
    height: 90px;
    background-color: black;
    opacity: 1;
    transition: opacity, height .5s ease-in-out;
    /* 初始透明度为0 */
    /* opacity: .5;  */
}

.floatingFrame {
    width: 100%;
    height: 100%;
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
    color: #ffff;
}

.fontStyle {
    color: #20303c;
    flex-shrink: 0;
    font-family: wixFreemiumFontW01-65Medi, wixFreemiumFontW02-65Medi, wixFreemiumFontW10-65Medi, Helvetica Neue, Helvetica, Arial, メイリオ, meiryo, ヒラギノ角ゴ pro w3, hiragino kaku gothic pro, sans-serif;
    font-size: 0.175rem;
    line-height: 24px;
    /* font-weight: 500; */

}

.invisibleButton {
    align-items: center;
    border: 1px solid #a0138e;
    border-radius: 17px;
    color: #a0138e;
    display: inline-flex;
    flex-shrink: 0;
    font-family: wixFreemiumFontW01-65Medi, wixFreemiumFontW02-65Medi, wixFreemiumFontW10-65Medi, Helvetica Neue, Helvetica, Arial, メイリオ, meiryo, ヒラギノ角ゴ pro w3, hiragino kaku gothic pro, sans-serif;
    font-size: 0.175rem;
    height: 35px;
    justify-content: center;
    text-align: center;
    width: 112px;

}

.linkElement {
    align-items: center;
    background: #eff1f2;
    border-bottom: 3px solid #a0138e;
    box-sizing: border-box;
    display: flex;
    height: 50px;
    justify-content: center;
    width: 100%;
}

.linkElement:hover .invisibleButton {
    background: #a0138e;
    color: #eff1f2;
    cursor: pointer;
}

.svgFather {
    height: 50px;
    flex: 1;
}

.rightNavigation {
    flex: 3;
}

.svgSize {
    width: 206px;
    height: 50px;
}

/* navlink导航 */
.navLinkButTop {
    position: relative;
    right: 0px;
    bottom: -20px;
}

.navLinkBut {
    font-size: 0.19rem;
    position: absolute;
    right: 76px;
    width: 300px;
    height: 50px;
    display: flex;
    justify-content: space-around;
    text-align: center;
}

.spinLoading {
    position: fixed;
    left: 0;
    right: 0;
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    /* 水平居中 */
    align-items: center;
    /* 垂直居中 */
    background: rgba(0, 0, 0, 0.3);
    /* 使用 rgba 设置半透明背景颜色 */
}